*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Roboto";
  margin: 0;
  padding: 0;
}

.text-center {
  text-align: center;
}

.btn {
  border: 1px solid #000;
  border-radius: 2px;
  font-size: 14px;
  padding: 8px 20px;
}

.btn--login {
  margin-top: 15px;
}

.nav {
  padding: 25px 25px;
}

.nav .nav__brand {
  font-size: 24px;
}

.nav .nav__list {
  display: none;
  float: right;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav .nav__item {
  display: inline;
  margin-right: 15px;
}

@media (min-width: 600px) {
  .nav .nav__list {
    display: inline;
  }
}

.card {
  border-radius: 2px;
  padding: 25px 20px;
  margin: 0 10px;
  border: 1px solid #000;
}

@media (min-width: 600px) {
  .card {
    max-width: 400px;
    margin: auto;
  }
}

.card .card__header {
  margin-bottom: 30px;
}

.card .card__heading {
  font-size: 26px;
  margin-bottom: 4px;
}

.card .card__subheading {
  font-size: 16px;
}

.form .form__group {
  margin-bottom: 15px;
}

.form .form__label,
.form .form__input {
  display: block;
}

.form .form__label {
  margin-bottom: 10px;
}

.form .form__input {
  border: 1px solid #000;
  font-size: 14px;
  padding: 6px 4px;
  width: 96%;
}

.form .form__input:focus {
  outline: none;
}

.form .form__link {
  margin-bottom: 10px;
}

.landing .container__content {
  margin-top: 20vh;
}

.landing .landing__heading {
  font-size: 30px;
}

.landing .landing__subheading {
  margin: 4px 0 40px 0;
}

@media (min-width: 600px) {
  .section-register .card {
    margin-top: 10vh;
  }
}



